import { useEffect, useState } from "react";
import Footer from "../components/Footer";
import Header from "../components/Header";

import { datas } from '../datas.js';

function Process() { 

    const [isMobile, setIsMobile] = useState(false);
    const process = datas.process;

    // Vrifier si l'cran est mobile lors du montage du composant
    useEffect(() => {
        updateScreen();
        window.addEventListener('resize', updateScreen);
        return () => window.removeEventListener('resize', updateScreen);
    }, []);

    const imgClassName = isMobile ? 'mobile-class' : 'desktop-class';


    const updateScreen = () => {
        setIsMobile(window.innerWidth <= 990);
    }

    return (
        <>
            <Header background="metiers" text="Facilitez vos dmarches aprs un sinistre"/>
            <div className="pb-32"></div>

            <div>
                {/* Titre gnral */}
                <div className="flex justify-center w-2/3 m-auto pb-20">
                    <h1 className="font-primary text- uppercase text-center text-3xl sm:text-5xl pb-5 border-b-4 border-primary tracking-widest transition-all ease-in-out duration-500">Apporter de lingnierie au bon moment</h1>
                </div>

                {/* Introduction */}
                <div className="font-primary text-center text-lg sm:text-xl w-2/3 m-auto">
                    <p className="text-primary my-10 mt-0">DETERMINANT intervient en fonction de ltat davancement du dossier de sinistre et sadapte aux besoins du contexte</p>
                </div>
                {/* progress */}
                {imgClassName === 'mobile-class' ? 
                    process.map((item) => (
                        <div key={item.id} style={{backgroundColor: item.color}} className="flex justify-center items-center flex-col p-16 w-5/6 m-auto rounded-3xl mb-10">
                            <img src={item.icon} alt="placeholder" className="h-16 w-16"/>
                            <h2 className="text-xl text-center text-white uppercase pt-5">{item.title}</h2>
                            <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-white"></hr>
                            <p className="text-center text-white text-lg">{item.description}</p>
                        </div>
                    ))
                :
                <div className="bg-white w-full h-[2420px] relative flex justify-center">
                    <img src="../images/process/process_animated.svg" alt="placeholder" className="w-64 m-auto absolute top-0"/>
                    {/* diagnostic */}
                    <div className="absolute top-[50px] mr-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#00628B] uppercase">diagnostic</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#00628B]"></hr>
                        <p className="text-center">Consiste en une valuation dtaille de l'tat d'un btiment ou d'une structure existante afin d'identifier les problmes potentiels</p>
                    </div>
                    {/* matrise d'oeuvre */}
                    <div className="absolute top-[290px] ml-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#436EFB] uppercase">matrise d'oeuvre</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#436EFB]"></hr>
                        <p className="text-center">Englobe l'ensemble des tapes lies  la conception du projet, de sa planification initiale  sa finalisation.</p>
                    </div>
                    {/* challenger une rclamation maximaliste */}
                    <div className="absolute top-[530px] mr-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#FFAE55] uppercase">challenger une rclamation maximaliste</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#FFAE55]"></hr>
                        <p className="text-center">Parvenir  un rglement quitable et quilibr, en prenant en compte les intrts et les droits des deux parties impliques.</p>
                    </div>
                    {/* prconisation de solutions optimises */}
                    <div className="absolute top-[765px] ml-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#7D4CD5] uppercase">prconisation de solutions optimises</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#7D4CD5]"></hr>
                        <p className="text-center">Consiste  proposer des solutions techniques et pratiques qui rpondent de manire optimale aux besoins et aux objectifs du projet.</p>
                    </div>
                    <div className="absolute top-[1015px] mr-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#BE1622] uppercase">ingnierie et modlisation</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#BE1622]"></hr>
                        <p className="text-center">Permet de crer des reprsentations virtuelles en 3D du projet,  l'aide de logiciels spcialiss.</p>
                    </div>
                    <div className="absolute top-[1235px] ml-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#00628B] uppercase">ingnierie pour compte</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#00628B] "></hr>
                        <p className="text-center">DETERMINANT engage des ingnieurs spcialiss pour travailler en leur nom. Ces ingnieurs agissent en tant que reprsentants de l'entreprise pour grer les aspects techniques du projet.</p>
                    </div>
                    <div className="absolute top-[1450px] mr-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#436EFB] uppercase">sapiteur technique d'expert d'assurance</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#436EFB]"></hr>
                        <p className="text-center">Professionnel mandat par la compagnie d'assurance pour valuer les dommages, les pertes et les responsabilits lis au sinistre. Le sapiteur technique possde une expertise approfondie dans le domaine de la construction et des travaux, lui permettant d'analyser les lments techniques du projet, </p>
                    </div>
                    <div className="absolute top-[1715px] ml-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#FFAE55] uppercase">avis sur dossier</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#FFAE55]"></hr>
                        <p className="text-center">Consiste en l'examen approfondi et l'valuation d'un dossier de projet, comprenant les plans, les spcifications techniques, les tudes et les documents pertinents. </p>
                    </div>
                    <div className="absolute top-[1905px] mr-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#7D4CD5] uppercase">assistance technique en technique et expertise judiciaire</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#7D4CD5] "></hr>
                        <p className="text-center">Professionnels spcialiss dans ce domaine apportent leur expertise technique pour assister les parties impliques dans le processus judiciaire. Ils analysent les lments techniques du dossier, valuent les dommages, les malfaons ou les responsabilits, et fournissent des rapports d'expertise dtaills et impartiaux.</p>
                    </div>
                    <div className="absolute top-[2175px] ml-[650px] m-auto w-1/6">
                        <h2 className="text-xl text-center text-[#BE1622] uppercase">package diagnostic, matrise d'oeuvre, chiffrage des travaux</h2>
                        <hr className="w-1/3 m-auto border-2 mb-5 mt-2 border-[#BE1622]"></hr>
                        <p className="text-center">Englobe l'valuation dtaille de l'tat du btiment, l'identification des problmes potentiels et des travaux ncessaires, ainsi que la collecte d'informations essentielles pour la planification du projet.</p>
                    </div>
                </div>
                }

            </div>
            <div className="pb-32"></div>
            <Footer/>
        </>
    )
}
export default Process;